
import { isScrollBottom } from "@/utils/comm.js";
import { mapGetters, mapActions, mapMutations } from "vuex";
import { api } from "@/utils/api.js";
import { wxPay } from "@/utils/wx_sdk.js";
import loading from "@/components/loading.vue";
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Badge from 'vux/src/components/badge/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'

export default {
name: "shop",
props: {},
components: {
Group,
Cell,
XButton,
Badge,
XHeader,
loading
},
data() {
return {
isshow: 1,
tabindex: 1, //tab
upLoading: true,
upLoadingComplete: false,
page: 1,
listInfo: []
};
},
created() {
this.tabindex = this.$route.query.tabindex || 1;
this.showdata();
},
mounted: function() {
this.$nextTick(function() {
isScrollBottom(this.showdata);
});
},
destroyed() {
window.onscroll = null;
},
methods: {

tabnav(e) {
this.tabindex = e.currentTarget.dataset.index;
this.page = 1;
this.isshow = 1;
this.upLoading = true;
this.upLoadingComplete = false;
this.listInfo = [];
this.showdata();
},

async showdata() {
if (this.upLoadingComplete) {
return;
}
let data = {
plat: 3,
account: this.account,
token: this.token,
type: this.tabindex,
page: this.page,
version: "1.0.0"
};
this.page++;
this.upLoading = true;
const [err, res] = await api.getneworderlist(data);
if (res && res.code == "2000") {
if (!res.data.list.length) {
console.log(res.data.list, this.page);
if (this.page == 2) {
this.isshow = 2;
this.upLoading = false;
this.upLoadingComplete = false;
return;
}
this.upLoading = false;
this.upLoadingComplete = true;
return;
}
if (res.data.list.length < 5) {
this.showdata();
}
this.listInfo = this.listInfo.concat(res.data.list);
this.upLoading = false;
}
},
openInfo(e) {
let order_id = e.currentTarget.dataset.orderid;
let order_sn = e.currentTarget.dataset.ordersn;
this.$router.push({
path: "/centerFull/orderFull/orderlistinfo",
query: {
order_id,
order_sn
}
});
},

async quxiaoOrder(e) {
let that = this;
let orderid = e.currentTarget.dataset.orderid;
let deleteType = e.currentTarget.dataset.deletetype;
let index = e.currentTarget.dataset.index;
let token = this.token;
let account = this.account;
let title = "",
content = "";
if (deleteType == 1) {
title = "取消订单";
content = "确认取消订单？";
} else {
content = "确认删除订单？";
title = "删除订单";
}
this.$vux.confirm.show({
title,
content,
async onConfirm() {
that.$vux.loading.show({
text: "处理中..."
});
let data = {
plat: 3,
account: that.account,
token: that.token,
order_id: orderid,
type: deleteType
};
const [err, res] = await api.cancelorder(data);
that.$vux.loading.hide();
if (err) {
that.$vux.toast.text(err.msg);
return;
}
that.$vux.toast.text(res.msg);
that.listInfo.splice(index, 1);
}
});
},
async gobay(orderid) {
this.$vux.loading.show({
text: "支付中..."
});
let that = this;
let data = {
plat: 3,
account: this.account,
token: this.token,
order_id: orderid,
pay_code: 2,
openid: this.user.openid
}
const [err, res] = await api.orderunpaidpay(data);
if (err) {
that.$vux.toast.text(err.msg);
return;
}
if(res.code == 2000){
var selfData = res;
let success = res =>{
that.$router.push({
path: '/centerFull/orderFull/orderlistinfo',
query: {
order_id: selfData.data.order_id,
order_sn: selfData.data.data.order_sn,
}
})
}
let fail = err =>{

}
wxPay(this,{...res.data.pay_param,success,fail})
}
},
tixingFahuo(orderid) {
let that = this;
this.$vux.confirm.show({
title: '',
content: '催发货后商家将会收到提醒，只能提醒发货一次哦～',
async onConfirm(){
let data = {
plat: 3,
account: this.account,
token: this.token,
order_id: orderid
}
const [err, res] = await api.shipnotify(data);
if (err) {
that.$vux.toast.text(err.msg);
return;
}
if(res.code == 2000){
that.$vux.toast.text('提醒成功');
}
},
onCancel(){}
})
},
buyagain(goodsId) {
this.$router.push({
path: '/index/goodsInfoPindan',
query: {
goodsId
}
})
},
goShip(order_id,shipping_code) {
this.$vux.text('开发中...')
return
this.$router.push({
path: 'order_ship',
query: {
order_id,
shipping_code
}
})
},
receiving(orderid,index) {
let that = this;
this.$vux.confirm.show({
title: '确认收货',
content: '确认收到货物？',
confirmColor: "#FB4C72",
async onConfirm(){
that.$vux.loading.show({
text: '请求中...'
})
let data = {
plat: 3,
order_id: orderid,
token: that.token,
account: that.account,
}
const [err, res] = await api.orderreceive(data);
if (err) {
that.$vux.toast.text(err.msg);
return;
}
if(res.code == 2000){
that.listInfo.splice(index, 1);
that.$vux.toast.text('收货成功');
}
},
onCancel(){}
})
},

goeva(e) {
let orderid = e.currentTarget.dataset.orderid;
let itemId = e.currentTarget.dataset.itemid;
this.$router.push({
path: "/indexFull/goodsFull/fbPingjia",
query: {
orderid,
itemId
}
});
},
refund(refundid) {
this.$router.push({
path: '/centerFull/orderFull/afterlistInfo',
query: {
refund_id: refundid
}
})
},
...mapActions(["update_storeInfo"])
},
computed: {
...mapGetters(["user", "account", "token"])
}
};



